









import { Component, Prop, Vue } from 'vue-property-decorator';
@Component({
    props: [
        'placeholder',
        'doSearch',
        'value',
    ],
})
export default class FTBSearchBar extends Vue {
    @Prop()
    public buttonClick!: () => void;

    @Prop()
    public doSearch!: () => void;

    public handleClick() {
        this.buttonClick();
    }
}
